


































import Vue from 'vue'
import axios from 'axios'
import Loader from '@/components/Loader.vue'

export default Vue.extend({
    name: "Projects",
    components: {
      Loader
    },
    data: function(){
        return{        
          OnLoad:true,
          ProjectsData: null,
          lan: null
        }

    },
    computed:{
      PageData(){
        if(this.lan == 'ru'){
          return {
            Header: ' ',
            Details: '...'
          }
        }
        else{
          return {
            Header: 'My projects',
            Details: 'More details...'
          }
        }
      }
    },
    watch:{
      $route() {this.FetchData();}
    },
    mounted(){
      this.FetchData();
    },
    methods:{
      FetchData(){
        this.OnLoad=true;
        axios
        .get(this.$data.ServerLink +'/getProjects?language='+this.$route.params.lan)
        .then(response => {
          if (response.data.status=="error")
          console.log(response.data.message);
          else
          this.ProjectsData = response.data;
        })
        .catch(error => {
          console.log(error);
        })
        .finally(()=>{this.OnLoad=false;})
        this.lan = this.$route.params.lan;
      }
  } 
})
